  export const Login = async (email,password) => {
      console.log(email);
    try {
      await signInWithEmailAndPassword(
        auth,
        email,
        password
      );

      toast("Success", { type: "success" });
    } catch (err) {
        toast(err.message, { type: "error" });
    }
  };

  export const Logout = async () => {
    await signOut(auth);
    toast("Logout", { type: "error" });
  };
