import React from "react";
import {Login} from "Auth"

const Auth = () => {

const handleSubmit=async(e)=>{

  e.preventDefault();
  console.log(e.target.email.value,e.target.password.value);
  let email=e.target.email.value;
  let password=e.target.password.value;
  await Login(email,password);

}

  return (
    <div style={{ borderBottom: "3px solid black" }}>
      <form onSubmit={handleSubmit}  className="d-flex flex-wrap align-content-center justify-content-around justify-content-lg-end">
        <input
          type="email"
          className="rounded mb-2"
          placeholder="Email"
          name="email"
          required
        />
        <input
          type="password"
          className="rounded mb-2"
          placeholder="Password"
          name="password"
          required
        />
        <button
          type="submit"
          className="btn mb-2 rounded px-4 text-white"
          style={{ background: "rgba(0, 0, 0, 0.7)" }}
        >
          Se connect√©
        </button>
      </form>
    </div>
  );
};

export default Auth;
